import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import {
  Accordion,
  AccordionDetails,
  AccordionSummary,
  CircularProgress,
  Container,
  Typography,
} from "@mui/material";
import Grid from "@mui/material/Grid";
import { createLazyFileRoute } from "@tanstack/react-router";
import hintenLicenses from "../../resources/hinten-licenses.json";
import licenses from "../../resources/vorne-licenses.json";

export const Route = createLazyFileRoute("/licenses")({
  component: LicensesPageComponent,
});

function LicensesPageComponent() {
  if (!licenses || !hintenLicenses) {
    return <CircularProgress />;
  }

  return (
    <Container maxWidth="lg" sx={{ marginTop: 8, padding: 4 }}>
      <Grid container spacing={4}>
        <Grid item xs={12} md={6}>
          <Typography variant="h4">Backend Licenses</Typography>
          {Object.entries(hintenLicenses).map(
            (
              [
                _,
                {
                  License,
                  Name,
                  Description,
                  URL,
                  LicenseText,
                  Author,
                  Version,
                },
              ],
              index,
            ) => (
              <Accordion key={index}>
                <AccordionSummary expandIcon={<ExpandMoreIcon />}>
                  <Typography variant="h6">
                    {Name}@{Version}
                  </Typography>
                </AccordionSummary>
                <AccordionDetails>
                  <Typography variant="body1">
                    <strong>Description:</strong> {Description}
                  </Typography>
                  <Typography variant="body1">
                    <strong>Author:</strong> {Author}
                  </Typography>
                  <Typography variant="body1">
                    <strong>Repository: </strong>
                    <a href={URL} target="_blank" rel="noopener noreferrer">
                      {URL}
                    </a>
                  </Typography>
                  <Typography variant="body1">
                    <strong>License:</strong> {License}
                  </Typography>
                  <br />
                  {LicenseText && (
                    <Typography variant="body1">
                      <pre>{LicenseText}</pre>
                    </Typography>
                  )}
                </AccordionDetails>
              </Accordion>
            ),
          )}
        </Grid>

        <Grid item xs={12} md={6}>
          <Typography variant="h4">Frontend Licenses</Typography>
          {Object.entries(licenses).map(([key, details]) => (
            <Accordion key={key}>
              <AccordionSummary expandIcon={<ExpandMoreIcon />}>
                <Typography variant="h6">{key}</Typography>
              </AccordionSummary>
              <AccordionDetails>
                <Typography variant="body1">
                  <strong>License:</strong> {details.licenses}
                </Typography>
                <Typography variant="body1">
                  <strong>Repository: </strong>
                  <a
                    href={details.repository}
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    {details.repository}
                  </a>
                  <Typography variant="body1">
                    <strong>License File:</strong> {details.licenseFile}
                  </Typography>
                </Typography>
              </AccordionDetails>
            </Accordion>
          ))}
        </Grid>
      </Grid>
    </Container>
  );
}
